.Homepage_total {
    width: 100%;
    background-color: rgb(39, 38, 38);
    padding-top: 100px;
    color: white;
    display: inline-block;
    justify-content: center;
    height: 400vh;
    background-image: url('../Images/home_back5.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    
}

.first_text {
    width: 50%;
    height: 20vh;
    background-image: url('../Images/yellow_say.png');
    background-size: contain;
    background-repeat: no-repeat;
    margin-top: 54%;
    margin-left: 10%;
}

.t {
    color: white;
    font-size: 3vw;
    
}
.last_result {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-bottom: 20vh;
    margin-top: 0;
}

.last_result_info {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.Imgbox {
    width: 40%;
}
.textbox {
    text-align: center;
    font-size: 5vw;
    width: 20%;
    color: black;
    font-weight: bold;
    margin-Top : 30%;
}
.TeamA{
    background-image: url('../Images/Hansol_finalmember.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20em 0 2em 2em;
}
.TeamB{
    background-image: url('../Images/Afreeca_finalmember.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0 20em 2em 2em;
}


/* MVP와 우승팀 나오는 div */
.sub_result {
    display: flex;
    width: 80%;
    height: max-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10vh;
}
.mvpbox {
    width: 40%;
    height: 80vh;
}
.winnerteambox{
    width: 55%;
    margin-left: auto;
}
.mvp_image {
    width: 100%;
    height: 70%;
    background-image: url('../Images/mvp_imgae.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}
.winner_team_info{
    height: 100%;
    font-size: 1vw;
    color : white;
    background-image: url('../Images/black_say.png');
    background-size: contain;
    background-repeat: no-repeat;
    padding-left: 5px;
    padding-bottom: 35px;     
    padding-right: 0; 
    margin-top: 30px;  
    font-weight: bold;
}
.mvp_info h1 {
    color : black;
}
.mvp_info{
    height: 39%;
    font-size: 1vw;
    color: black;
    background-image: url('../Images/gray_say.png');
    background-size: contain;
    background-repeat: no-repeat;
    padding-left: 7vw;
    padding-top: 30px;   
    margin-bottom: 30px; 
    font-weight: bold;
}
.winnerteam_image{
    width: 100%;
    height: 60%;
    background-image: url('../Images/winner_team.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    border-style: none;
}

.footer {
    width: 100%;
    height: 40vh;
    background-image: url('../Images/footer.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
